<template>
  <div class="section">
    <div class="main">
      <h2 class="section-label">Tree </h2>
      <h4></h4>
      <div class="demo-block">
        <v-tree class="item" :model="tree"  style="width:200px;display:inline-block"></v-tree>
  
      </div>
      <h4></h4>
      <div class="demo-block">
        <v-tree class="item" :model="tree" showIcon style="width:200px;display:inline-block"></v-tree>
  
      </div>
      <h4></h4>
      <div class="demo-block">
        <v-tree class="item" :model="tree" show-checkbox style="width:200px;display:inline-block;    vertical-align: top;"></v-tree>
  
      </div>
  
    </div>
  </div>
</template>

<script>
  import VTree from "../../common/tree";
  
  export default {
    components: {
      VTree
  
    },
    data() {
      return {
        "tree": {
          "grade": 0,
          "name": "",
          "children": [{
              "grade": 11,
              "parent": 0,
              "name": "",
              "children": [{
                  "grade": 2111,
                  "parent": 211,
                  "name": "sss",
                  "children": [{
                    "grade": 21111,
                    "parent": 2111,
                    "name": ""
                  }]
                },
                {
                  "grade": 2112,
                  "parent": 211,
                  "name": "",
                  "children": [{
                    "grade": 21121,
                    "parent": 2112,
                    "name": ""
                  }]
                }
              ]
            },
            {
              "grade": 12,
              "parent": 0,
              "name": "",
              "children": [{
                  "grade": 221,
                  "parent": 12,
                  "name": ""
                },
                {
                  "grade": 222,
                  "parent": 12,
                  "name": ""
                },
                {
                  "grade": 223,
                  "parent": 12,
                  "name": ""
                }
              ]
            },
            {
              "grade": 13,
              "parent": 0,
              "name": "",
              "children": [{
                "grade": 231,
                "parent": 13,
                "name": ""
              }]
            }
          ]
        },
      }
    },
    created() {
      this.$axios.get("http://localhost:8000/api/getTree").then(res => {
        this.tree = res.data.data;
      });
    },
  }
</script>

<style lang="scss" scoped>
  
</style>




