<template>
    <div class="section">
        <div class="main">
            <p class="section-label">Dialog </p>
            <h4></h4>
            <div class="demo-block">
                <v-button type="primary" size="small" @click="isVisible1=true"></v-button>
                <v-dialog :visible.sync="isVisible1" title="" size="small" center @open="openHandle" :closeOnClickModal=false>
                    <p></p>
                    <p></p>
                    <p></p>
                </v-dialog>
            </div>
            <h4></h4>
    
            <div class="demo-block">
                <v-button type="primary" size="small" @click="isVisible2=true"></v-button>
                <v-dialog :visible.sync="isVisible2" size="small" center @open="openHandle" :closeOnClickModal=false>
                    <p></p>
                    <p></p>
                    <p></p>
                </v-dialog>
            </div>
            <h4></h4>
    
            <div class="demo-block">
                <v-button type="primary" size="small" @click="isVisible3=true"></v-button>
    
                <v-dialog :visible.sync="isVisible3" title="Modal Title" ok-text="OK" cancel-text="Cancel" size="tiny" center @on-ok="ok()" @on-cancel="cancel()">
                    <p></p>
                    <p></p>
                    <p></p>
                </v-dialog>
            </div>
            <h4></h4>
            <div class="demo-block">
                <v-button type="primary" size="small" @click="isVisible4=true"></v-button>
                <v-dialog :visible.sync="isVisible4" size="small" center @open="openHandle">
                    <p slot="header" style="color:#f60;text-align:center">
                        <font-awesome-icon :icon="['fas','exclamation-circle' ]" />
                        <span></span>
                    </p>
                    <div style="text-align:center">
                        <p> 10 </p>
                        <p></p>
                    </div>
                    <div slot="footer">
                        <v-button type="danger" size="large"></v-button>
                    </div>
                </v-dialog>
            </div>
    
    
    
    
        </div>
    </div>
</template>

<script>
    import VDialog from "../../common/dialog"
    import VButton from "../../common/button"
    export default {
        components: {
            VDialog,
            VButton
        },
        data() {
            return {
                isVisible1: false,
                isVisible2: false,
                isVisible3: false,
                isVisible4: false,
            }
        },
        methods: {
            ok() {
                this.$vmessage.info("");
            },
            cancel() {
                this.$vmessage.info("");
            },
            openHandle() {}
        }
    }
</script>



