<template>
    <div class="section">
        <div class="main">
            <p class="section-label">Select</p>
            <h4></h4>
            <div class="demo-block">
                <v-select :selectValues="cityList" :v-model="selectValueModel" placeholder=""  @change="onSelectChange($event,'selectValue')"></v-select>
            </div>
            <h4></h4>
            <div class="demo-block">
                <v-select :selectValues="cityList" placeholder="" disabled size="small"></v-select>
            </div>
            <h4></h4>
            <div class="demo-block">
                <v-select :selectValues="cityList" :v-model="selectValueModel"  placeholder="" clearable @change="onSelectChange($event,'selectValue')"></v-select>
            </div>
            <h4></h4>
            <div class="demo-block">
                <v-select placeholder="" notFoundText='' size="large" :style="{width:'100px'}"></v-select>
            </div>
        </div>
    </div>
</template>

<script>
    import VSelect from "../../common/select"
    export default {
        components: {
            VSelect
        },
        data() {
            return {
                selectValueModel: "",
                cityList: [{
                        value: "beijing",
                        label: ""
                    },
                    {
                        value: "shanghai",
                        label: ""
                    },
                    {
                        value: "shenzhen",
                        label: ""
                    },
                    {
                        value: "hangzhou",
                        label: ""
                    },
                    {
                        value: "nanjing",
                        label: ""
                    },
                    {
                        value: "beijing",
                        label: ""
                    },
                    {
                        value: "beijing",
                        label: ""
                    },
                    {
                        value: "chongqing",
                        label: ""
                    }
                ]
            }
        },
        methods: {
            onSelectChange(v, type) {
                console.log(v, type);
                this[type] = v;
            },
        }
    
    }
</script>





