
<template>
    <div class="section">
        <div class="main">
            <p class="section-label">Toast </p>
            <h4></h4>
            <div class="demo-block">
                <v-button type="primary" size="small" @click="showToast">Toast</v-button>
            </div>
            <h4></h4>
            <div class="demo-block">
                <v-button type="primary" size="small" @click="showIconToast">IconToast</v-button>
            </div>
            <h4></h4>
            <div class="demo-block">
                <v-button type="primary" size="small" @click="showPosToast">Toast</v-button>
            </div>
        </div>
    </div>
</template>

<script>
    import VButton from "../../common/button"
    
    export default {
        components: {
            VButton
        },
        methods: {
            showToast() {
                this.$vtoast("");
            },
            showIconToast() {
                this.$vtoast({
                    text: "",
                    duration: "2000",
                    icon: "user"
                });
            },
            showPosToast() {
                this.$vtoast({
                    text: "",
                    position: "top"
                });
            },
        }
    }
</script>

<style lang="scss" scoped>
    .main {
        margin: 0 20px;
    }
    
    .section-label {
        color: #657180;
        font-weight: bold;
        font-size: 18px;
        margin: 0 0 10px 0;
    }
</style>





